<template>
<div id="app">
    <div class="btn-col">
        <input type="text" v-model="name" placeholder="请输入人名">
        <button @click='addItem'>add</button>
    </div>
    <table v-if="list.length">
        <thead>
            <tr>
                <th>姓名</th>
                <th>审核</th>
                <th>操作</th>
            </tr>
        </thead>
        <tbody>
            <tr v-for="(item, index) in list" :key="item.id">
                <td>{{ item.name }}</td>
                <td>{{ item.statu }}</td>
                <td>
                    <button v-if="item.statu === '待审核'" @click='checkItem(index)'>审核</button>
                    <button v-else @click='delItem(index)'>删除</button>
                </td>
            </tr>
        </tbody>
    </table>
    <div v-else>
        当且没有需要审核或者删除的列表~
    </div>
</div>
</template>

<script>
export default {
    name: 'app',
    data() {
        return {
            name: '',
            list: [
                {
                    id: 1,
                    name: '张三',
                    statu: '合格'
                },
                {
                    id: 2,
                    name: '李四',
                    statu: '不合格'
                },
                {
                    id: 3,
                    name: '王五',
                    statu: '待审核'
                },
                {
                    id: 4,
                    name: '赵六',
                    statu: '待审核'
                },
                {
                    id: 5,
                    name: '孙七',
                    statu: '待审核'
                },
            ],
            vmNextId: 6
        }
    },
    methods: {
        addItem() {
            if (this.name.replace(/\s/g, '') !== '') {
                this.list.push({
                    id: this.vmNextId++,
                    name: this.name,
                    statu: '待审核'
                });
            } else {
                alert('请输入正确的人名');
            }
            this.name = '';
        },
        delItem(index) {
            this.list.splice(index, 1);
        },
        checkItem(index) {
            this.list[index].statu = '合格';
        }
    }
}
</script>

<style scoped>
[v-cloak] {
    display: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
    empty-cells: show;
    margin-top: 8px;
}
th, td {
    padding: 8px 16px;
    border: 1px solid #dddddd;
    text-align: center;
}
th {
    background: #f7f7f7;
    color: #5c6b77;
    white-space: nowrap;
}
</style>

